@use 'base/variables' as v;
@use 'base/mixins' as m;

.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
        align-items: flex-end;
    }
    .dark-mode-boton{
        width: 3rem;
        margin-top: 2rem;
        margin-bottom: 2rem;
        filter: invert(100%);
        &:hover{
            cursor: pointer;
        }
        @include m.tablet {
            margin-top: 0;
        }
    }

}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion{
    visibility: hidden; /* Ocultar navegación */
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .3s linear, height .3s linear;
    @include m.tablet {
        display: flex;
        gap: 2rem;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a{
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        @include m.tablet {
            font-size: 1.8rem;
        }
        &:hover{
            color: v.$verde;
        }
    }
}

/** Burger menu **/
.mobile-menu{
    padding-top: 1.5rem;
    cursor: pointer;
    @include m.tablet{
        display: none;
    }

    img{
        width: 4rem;
    }
}
.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}