@use 'variables' as v;

.seccion {
    margin-top: calc(v.$separacion / 2);
    margin-bottom: calc(v.$separacion / 2);
}
.contenido-centrado {
    max-width: 80rem;
}

.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}

.w-100{
    width: 100%;
}

.separacion-gap {
    display: flex;
    gap: calc(v.$separacion / 2);
}